<script>
const tposts = require('../assets/postData.json');

export default {
  components: {
  },
  mounted(){
    const onClick = (event) => {
      var elementID = event.srcElement.id
      if(elementID[0] == "#"){
        this.currentHashTag = elementID.split("#")[1]
        this.getNewPost();
      }
    }
    window.addEventListener('click', onClick);
  },
  data(){
    return{
      tposts,
      currentHashTag: null,
      currentPostId: 0,
      viewedPosts : [0],
      errorMessages:{
        noPostsLeft: 'There are currently no more stories availiable, feel free to add one!',
        noPostsForHashTag : 'There are currently no stories availiable for this keyword, feel free to add one!'
      },
      currentErrorMessage : '',
      showErrorMessage: false,
    }
  },
  methods:{
    // Converts post to remove the # and turn following words into links
    convertPostText(text){ 
      return text.replace(/#(\w+)/g, `<a class="hashTag" id="#$1">$1</a>`)
    },
    getNewPost(){
      var tpostsLen = Object.keys(tposts).length;
      for(var i = 0; i < tpostsLen; i++){
        if(tposts[i]["hashtags"].includes('#' + this.currentHashTag) && !this.viewedPosts.includes(i)){
          this.currentPostId = i;
          this.viewedPosts.push(i);
          return;
        }
      }
      if(tpostsLen == this.viewedPosts.length) { this.currentErrorMessage = this.errorMessages.noPostsLeft; this.showErrorMessage = true; return;}
      else { this.currentErrorMessage = this.errorMessages.noPostsForHashTag; this.showErrorMessage = true; return; }
    }
  },
}
</script>


<template>
<span style="display: flex; justify-content: center; align-items: center;">
  <div>
      <p style="font-size: var(--font-large); font-weight: bold;"> Author: {{tposts[currentPostId]['author']}} </p>
      <page_soloBox>
          <template #body> 
              <div id="content">
                  <span v-html="convertPostText(tposts[currentPostId]['text'])"></span>
              </div>
          </template>
      </page_soloBox>
  </div>

  <div class="blackOut" v-if="showErrorMessage" @click="showErrorMessage = false"/>
  <div class="whiteBoxPopUp" v-if="showErrorMessage">
    <div class="close" @click="showErrorMessage = false">x</div>
     {{currentErrorMessage}}
  </div>
</span>
</template>

<style scoped>
.whiteBoxPopUp{
  position: absolute;
  width: 300px;
  height: fit-content;
  max-width: 50vw;
  max-height: 65vh;
  background: white;
  border: solid black 4px;

  display: flex;
  justify-content: flex-start;
  align-content: center;
  padding: 0.75em;

  font-size: 1.6em;
}

.close{
  width: 26px;
  height: 26px;
  position: absolute;
  background: white;
  border: solid black 4px;
  right: -12px;
  top: -12px;

  display: flex;
  justify-content: center;
  align-items: center;
  user-select: none;
}

.close:hover{
  cursor: pointer;
}

.blackOut{
  position: absolute;
  width: 100vw;
  height: 100vh;
  top: 0;
  left: 0;
  background: rgba(0, 0, 0, 0.75);
}
</style>


<style>
.hashTag{
  color: rgb(255, 0, 0);
  text-decoration: underline;
  cursor: pointer;
}
</style>